<template>
  <div class="container mt-5">
    <h1 class="text-center mb-4">Covid-19 Dashboard</h1>

    <!-- Date Range Filter -->
    <div class="mb-4 row">
      <div class="col-md-4">
        <label for="startDate" class="form-label">Start Date:</label>
        <input type="date" id="startDate" class="form-control" v-model="startDate" />
      </div>
      
      <div class="col-md-4">
        <label for="endDate" class="form-label">End Date:</label>
        <input type="date" id="endDate" class="form-control" v-model="endDate" />
      </div>
      
      <div class="col-md-4 d-flex align-items-end">
        <button class="btn btn-primary mt-3 w-100" @click="applyDateFilter">Apply Date Filter</button>
      </div>
    </div>

    <!-- Display Total and Cumulative Data for Indonesia -->
    <div class="card mb-4">
      <div class="card-header bg-primary text-white">
        <h2>Indonesia Data</h2>
      </div>
      <div class="card-body">
        <div class="row">
          <div class="col-md-4">
            <p><strong>Total Confirmed:</strong> {{ totalIndonesia.confirmed }}</p>
            <p><strong>Total Recovered:</strong> {{ totalIndonesia.recovered }}</p>
            <p><strong>Total Deaths:</strong> {{ totalIndonesia.deaths }}</p>
          </div>

          <div class="col-md-4">
            <p><strong>Cumulative Confirmed:</strong> {{ cumulativeIndonesia.confirmed }}</p>
            <p><strong>Cumulative Recovered:</strong> {{ cumulativeIndonesia.recovered }}</p>
            <p><strong>Cumulative Deaths:</strong> {{ cumulativeIndonesia.deaths }}</p>
          </div>

          <div class="col-md-4">
            <p><strong>Recovery Rate:</strong> {{ recoveryRateIndonesia }}%</p>
            <p><strong>Death Rate:</strong> {{ deathRateIndonesia }}%</p>
            <p><strong>Active Cases:</strong> {{ activeCasesIndonesia }}</p>
            <p><strong>Percentage of Confirmed Cases:</strong> {{ percentageIndonesia }}%</p>
          </div>
        </div>

        <Bar
          id="indonesiaChart"
          :options="chartOptions"
          :data="chartData"
        />
      </div>
    </div>

    <!-- Display Total and Cumulative Data for Global -->
    <div class="card mb-4">
      <div class="card-header bg-success text-white">
        <h2>Global Data</h2>
      </div>
      <div class="card-body">
        <div class="row">
          <div class="col-md-4">
            <p><strong>Total Confirmed:</strong> {{ totalGlobal.confirmed }}</p>
            <p><strong>Total Recovered:</strong> {{ totalGlobal.recovered }}</p>
            <p><strong>Total Deaths:</strong> {{ totalGlobal.deaths }}</p>
          </div>
          <div class="col-md-4">
            <p><strong>Cumulative Confirmed:</strong> {{ cumulativeGlobal.confirmed }}</p>
            <p><strong>Cumulative Recovered:</strong> {{ cumulativeGlobal.recovered }}</p>
            <p><strong>Cumulative Deaths:</strong> {{ cumulativeGlobal.deaths }}</p>
          </div>
          <div class="col-md-4">
            <p><strong>Recovery Rate:</strong> {{ recoveryRateGlobal }}%</p>
            <p><strong>Death Rate:</strong> {{ deathRateGlobal }}%</p>
            <p><strong>Active Cases:</strong> {{ activeCasesGlobal }}</p>
            <p><strong>Percentage of Confirmed Cases:</strong> {{ percentageGlobal }}%</p>
          </div>
        </div>

        <Bar
          id="globalChart"
          :options="chartOptions"
          :data="globalChartData"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { Bar } from 'vue-chartjs'
import { Chart as ChartJS, Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale } from 'chart.js'
import axios from 'axios';

// Register Chart.js components
ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale);

export default {
  name: 'DashboardComponent',
  components: { Bar },

  data() {
    return {
      startDate: '',
      endDate: '',
      chartData: {
        labels: [],  // Dynamic labels for dates
        datasets: []
      },
      globalChartData: {
        labels: [],  // Dynamic labels for dates
        datasets: []
      },
      totalIndonesia: {
        confirmed: 0,
        recovered: 0,
        deaths: 0,
      },
      totalGlobal: {
        confirmed: 0,
        recovered: 0,
        deaths: 0,
      },
      cumulativeIndonesia: {
        confirmed: 0,
        recovered: 0,
        deaths: 0,
      },
      cumulativeGlobal: {
        confirmed: 0,
        recovered: 0,
        deaths: 0,
      },
      percentageIndonesia: 0,  // Percentage for Indonesia
      percentageGlobal: 0,  // Percentage for Global

      // New analytics fields
      recoveryRateIndonesia: 0,
      deathRateIndonesia: 0,
      activeCasesIndonesia: 0,
      recoveryRateGlobal: 0,
      deathRateGlobal: 0,
      activeCasesGlobal: 0,

      chartOptions: {
        responsive: true,
        scales: {
          x: {
            title: {
              display: true,
              text: 'Date'
            }
          },
          y: {
            title: {
              display: true,
              text: 'Count'
            }
          }
        }
      }
    };
  },

  mounted() {
    this.fetchCovidData();  // Fetch data on mount
  },

  beforeUnmount() {
    // Destroy chart instances to prevent memory leaks
    if (this.indonesiaChart) {
      this.indonesiaChart.destroy();
    }
    if (this.globalChart) {
      this.globalChart.destroy();
    }
  },

  methods: {
    async fetchCovidData() {
      try {
        const response = await axios.get('http://localhost:3000/covid/dashboard');
        
        // Access and sort Indonesia data by date
        let sortedIndonesiaData = response.data.indonesiaData.sort((a, b) => new Date(a.date) - new Date(b.date));
        let sortedGlobalData = response.data.globalData.sort((a, b) => new Date(a.date) - new Date(b.date));

        // If startDate and endDate are selected, filter the data accordingly
        let filteredIndonesiaData = sortedIndonesiaData;
        let filteredGlobalData = sortedGlobalData;

        if (this.startDate && this.endDate) {
          const endDateWithExtraDay = new Date(this.endDate);
          endDateWithExtraDay.setDate(endDateWithExtraDay.getDate() + 1);  // Add 1 day to the end date

          filteredIndonesiaData = filteredIndonesiaData.filter(item => {
            const itemDate = new Date(item.date);
            return itemDate >= new Date(this.startDate) && itemDate <= endDateWithExtraDay;
          });
          filteredGlobalData = filteredGlobalData.filter(item => {
            const itemDate = new Date(item.date);
            return itemDate >= new Date(this.startDate) && itemDate <= endDateWithExtraDay;
          });
        }

        // Calculate total and cumulative values for Indonesia
        const labels = filteredIndonesiaData.map(item => new Date(item.date).toLocaleDateString());
        const confirmedData = filteredIndonesiaData.map(item => item.confirmed);
        const recoveredData = filteredIndonesiaData.map(item => item.recovered);
        const deathsData = filteredIndonesiaData.map(item => item.deaths);

        this.totalIndonesia.confirmed = confirmedData.reduce((acc, val) => acc + val, 0);
        this.totalIndonesia.recovered = recoveredData.reduce((acc, val) => acc + val, 0);
        this.totalIndonesia.deaths = deathsData.reduce((acc, val) => acc + val, 0);

        this.cumulativeIndonesia.confirmed = confirmedData[confirmedData.length - 1] || 0;
        this.cumulativeIndonesia.recovered = recoveredData[recoveredData.length - 1] || 0;
        this.cumulativeIndonesia.deaths = deathsData[deathsData.length - 1] || 0;

        // Calculate percentage of confirmed cases for Indonesia
        const percentIndonesia = (this.totalIndonesia.confirmed / (this.totalIndonesia.confirmed + this.totalIndonesia.recovered + this.totalIndonesia.deaths)) * 100;
        this.percentageIndonesia = percentIndonesia.toFixed(2);

        // Calculate recovery rate, death rate, and active cases for Indonesia
        const recoveryIndonesia = (this.totalIndonesia.recovered / this.totalIndonesia.confirmed) * 100;
        this.recoveryRateIndonesia = recoveryIndonesia.toFixed(2);
        const deathIndonesia = (this.totalIndonesia.deaths / this.totalIndonesia.confirmed) * 100;
        this.deathRateIndonesia = deathIndonesia.toFixed(2);
        this.activeCasesIndonesia = this.totalIndonesia.confirmed - (this.totalIndonesia.recovered + this.totalIndonesia.deaths);

        this.chartData = {
          labels,
          datasets: [
            {
              label: 'Confirmed Cases',
              data: confirmedData,
              backgroundColor: '#ff9999',
            },
            {
              label: 'Recovered Cases',
              data: recoveredData,
              backgroundColor: '#66b3ff',
            },
            {
              label: 'Deaths',
              data: deathsData,
              backgroundColor: '#99ff99',
            }
          ]
        };

        // Calculate total and cumulative values for Global
        const globalLabels = filteredGlobalData.map(item => new Date(item.date).toLocaleDateString());
        const globalConfirmedData = filteredGlobalData.map(item => item.confirmed);
        const globalRecoveredData = filteredGlobalData.map(item => item.recovered);
        const globalDeathsData = filteredGlobalData.map(item => item.deaths);

        this.totalGlobal.confirmed = globalConfirmedData.reduce((acc, val) => acc + val, 0);
        this.totalGlobal.recovered = globalRecoveredData.reduce((acc, val) => acc + val, 0);
        this.totalGlobal.deaths = globalDeathsData.reduce((acc, val) => acc + val, 0);

        this.cumulativeGlobal.confirmed = globalConfirmedData[globalConfirmedData.length - 1] || 0;
        this.cumulativeGlobal.recovered = globalRecoveredData[globalRecoveredData.length - 1] || 0;
        this.cumulativeGlobal.deaths = globalDeathsData[globalDeathsData.length - 1] || 0;

        // Calculate percentage of confirmed cases for Global
        const percentGlobal = (this.totalGlobal.confirmed / (this.totalGlobal.confirmed + this.totalGlobal.recovered + this.totalGlobal.deaths)) * 100;
        this.percentageGlobal = percentGlobal.toFixed(2);

        // Calculate recovery rate, death rate, and active cases for Global
        const recoveryGlobal = (this.totalGlobal.recovered / this.totalGlobal.confirmed) * 100;
        this.recoveryRateGlobal = recoveryGlobal.toFixed(2);
        const deathGlobal = (this.totalGlobal.deaths / this.totalGlobal.confirmed) * 100;
        this.deathRateGlobal = deathGlobal.toFixed(2);
        this.activeCasesGlobal = this.totalGlobal.confirmed - (this.totalGlobal.recovered + this.totalGlobal.deaths);

        this.globalChartData = {
          labels: globalLabels,
          datasets: [
            {
              label: 'Confirmed Cases',
              data: globalConfirmedData,
              backgroundColor: '#ff9999',
            },
            {
              label: 'Recovered Cases',
              data: globalRecoveredData,
              backgroundColor: '#66b3ff',
            },
            {
              label: 'Deaths',
              data: globalDeathsData,
              backgroundColor: '#99ff99',
            }
          ]
        };
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    },

    applyDateFilter() {
      this.fetchCovidData();  // Re-fetch data when filter is applied
    }
  }
};
</script>

